* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
a{
  text-decoration: none;
  columns: rgb(145, 73, 73);
}
body {
  margin: 0;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
  Oxygen, Ubuntu, Cantarell, 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  position: relative;
  height: 100vh;
  background-image: url("./images/windows.gif");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 80%;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
#main-content {
  display: flex;
  /* justify-content: center; */
  align-items: center;
  flex-direction: column;
}

.nav-bar {
display: flex;
align-items: center;
padding-block: .3rem;
justify-content: space-around;
width: 87%;
height: fit-content;
background-color:#c6c6c68a;
position: absolute;
bottom: 0;
left: 50%;
transform: translate(-50%, -50%);
border-radius: 10px;
font-size: 13px;
}
.nav-bar a:hover{
  transform: scale(1.15);
}
.nav-bar a img{
  width: 30px;
  height: 30px;
}
.nav-logo{
  width: 50px;
  animation: App-logo-spin infinite 20s linear;
}
.macos-text{
  font-size: 14px;
  padding-left: 40%;
  white-space: nowrap;
}
.macos-buttons{
  width: 10px;
  height: 10px;
  border-radius: 50%;
  border: none;
  margin: 5px;
  cursor: pointer;
}

.macos-button-box .red{
  background-color: #FF5F56;
}
.macos-button-box .yellow{
  background-color: #FFD700
}
.macos-button-box .green{
  background-color: #00FF00;
}
.macos-button-box{
  width: 100%;
  height: 20px;
  display: flex;
  padding-left: 1rem;
  background-color: #3d3d42;
  border-bottom: 1px solid #232327;
  border-top: none;
  border-left: none;
  border-right: none;
  border-top-left-radius: 15px;
  border-top-right-radius: 0px;
}

#container {
  background-color: hsla(240, 11%, 95%, 0.425);
  backdrop-filter: blur(3px);
  width: 90%;
  border-radius: 15px;
  height: 80vh;
  border: 2px solid #333;
  display: flex;
  position: relative;
  border: none;
}

.box {
  position: absolute;
  background-color: transparent;
  border: none;
  width: 90px;
  height: auto;
  max-width: 20 0px;
  margin: 2rem;
}
.top-dock{
  display: flex;
  justify-content: space-between;
  text-align: start;
  align-items: center;
  width: 100%;
  height: 1rem;
  color: whitesmoke;
  background-color: #06060895;
  margin-bottom: .8rem;
  padding-left: 1rem;
  gap: 1rem;
  white-space: nowrap;
  /* border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px; */
}
#top-dock-logo{
  margin-inline: .5rem;
  fill: white;

}
.top-dock-text{
  font-weight: 800;
}
.top-dock-right{
  display: flex;
  align-items: center;
  width: 4rem;
  margin-inline: 1rem;
  padding-bottom: .2rem;
  font-weight: 500;
  color: whitesmoke
}
.top-dock-left{
  font-size: 10px;
  color: whitesmoke;
  margin-inline: 1rem;
}

.splash-container{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #3d3d42;
  color: white;
  font-size: 2rem;
  text-align: center;
}

.splash-logo{
  animation: Grow infinite 7s linear;

}

#add-image-button{
  width: 70px;
  height: 20px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
    
  }
}
@media screen and (max-width: 600px) {
  .App {
    background-position: 20% 30%;
  }
  .nav-bar a img{
    width: 24px;
    height: 30px;
  }

  .macos-text{
    font-size: 10px;
    white-space: nowrap;
    text-align: center;
    margin-block: auto;
  }

  #container{
    width: 85%;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
    /* transform: rotateX(150deg); */
  }
}

@keyframes Grow{
  from {
    transform: scale(1);
  }
  to {
    transform: scale(1.3);
    /* transform: rotateX(150deg); */
  }
}
